<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">考勤日历</p>
        </div>
        <div class="calendarBox">
            <div class="calendar-top">
                <div class="l prev f26 c666" @click="prevMonth">上一月</div>
                <h2 class="l today f30 c333">{{today}}</h2>
                <div class="r next f26 c666" @click="nextMonth">下一月</div>
            </div>
            <div class="week">
                <div class="weekDay l">日</div>
                <div class="weekDay l">一</div>
                <div class="weekDay l">二</div>
                <div class="weekDay l">三</div>
                <div class="weekDay l">四</div>
                <div class="weekDay l">五</div>
                <div class="weekDay l">六</div>
            </div>
            <div class="monthbox">
                <div>
                    <div class="l days" v-for="(items,indexs) in emptyDay" :key="indexs"></div>
                </div>
                <div>
                    <div class="l days f22 c333" :class="{'cToday':item.status == 1,'cLeave':item.status == 2,'cLate':item.status == 3,'cBoth':item.status == 4}" v-for="(item,index) in listDay" :key="index" @click="clickThisDay(index)">{{item.num}}</div>
                </div>
            </div>
            <div class="chooseType">
                <router-link class="type-list" :to="{path:'/detailchange',query:{type:1,time:clickDay}}">
                    <div class="type-title l f26 c333">今日签到人数</div>
                    <img class="type-img r" src="../../../assets/my/more.png" alt="">
                    <div class="type-num r f22 c666">{{signCount}}人</div>
                </router-link>
                <router-link class="type-list" :to="{path:'/detailchange',query:{type:2,time:clickDay}}">
                    <div class="type-title l f26 c333">当日签退人数</div>
                    <img class="type-img r" src="../../../assets/my/more.png" alt="">
                    <div class="type-num r f22 c666">{{signBack}}人</div>
                </router-link>
                <router-link class="type-list borderNone" :to="{path:'/detailchange',query:{type:3,time:clickDay}}">
                    <div class="type-title l f26 c333">今日迟到人数</div>
                    <img class="type-img r" src="../../../assets/my/more.png" alt="">
                    <div class="type-num r f22 c666">{{lateNumber}}人</div>
                </router-link>
            </div>
            <div class="tuliBox">
                <div class="tu-list">
                    <img src="../../../assets/index/lateS.png" alt="">
                    <div class="tu-test f22 c666 ml-20 l">有迟到记录</div>
                </div>
                <div class="tu-list">
                    <img src="../../../assets/index/laveS.png" alt="">
                    <div class="tu-test f22 c666 ml-20 l">有请假记录</div>
                </div>
                <div class="tu-list">
                    <img src="../../../assets/index/bothS.png" alt="">
                    <div class="tu-test f22 c666 ml-20 l">有请假也有迟到记录</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            today:'',
            year:0,
            month:0,
            day:0,
            emptyDay:[],
            listDay:[],
            sign:{
                year:0,
                month:0
            },
            lateNumber:0,
            signBack:0,
            signCount:0,
            clickDay:'',//点击后的时间
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'})
        },
        prevMonth(){
            var year = this.year;
            var month = this.month;
            if(month == 1){
                this.month = 12;
                this.year = year - 1;
            }else{
                this.month = --this.month
            }
            this.getMonthDate(this.year,this.month,1);
            var mon = parseInt(this.month);
            mon = mon < 10 ? '0' + mon : mon; 
            this.clickDay = this.year + '-' + mon + '-01'
            this.getTimetag(this.clickDay);  
        },
        nextMonth(){
            var year = this.year;
            var month = this.month;
            if(month == 12){
                this.month = 1;
                this.year = year + 1;
            }else{
                this.month = ++this.month
            }
            this.getMonthDate(this.year,this.month,1);
            var mon = parseInt(this.month);
            mon = mon < 10 ? '0' + mon : mon;
            this.clickDay = this.year + '-' + mon + '-01'
            this.getTimetag(this.clickDay);   
        },
        getTody(){ // 获取今天日期
            var newDate = new Date();
            var year = newDate.getFullYear();
            var month = newDate.getMonth() + 1;
            var day = newDate.getDate();
            this.today = year + '年' + month + '月' + day +'日';
            this.year = year;
            this.month = month;
            this.day = day;
            this.sign = {
                year:year,
                month:month
            }
        },
        getMonthDate(year,month,day,status){ //status 0:默认日历 1：搜索日历
            month = month < 10 ? '0' + month : month;
            var param = {
                classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                time : year + '-' + month
            }
            
            var startNum = new Date(year + '-' + month).getDay();//本月第一天是星期几
            var endNum= new Date(year,month,0).getDate();
            // console.log(year,month,startNum,endNum)
            var arr = [];
            var arr1 = [];
            for(var i = 0; i < startNum;i++){
                arr.push({
                    day:""
                })
            }
            for(var i = 0; i < endNum;i++){
                arr1.push({
                    num:i+1,
                    status:0
                })
            }
            this.$http({url:this.$api.classattendance.classattendance},param).then(data => {
                console.log(data);  
                if(data.code == 0){
                     if(this.year == this.sign.year && this.month == this.sign.month){
                        arr1[this.day - 1].status = 1;//今天
                        this.today =  this.year + '年' + this.month + '月' + this.day +'日';
                    }else{
                        arr1[0].status = 1;//今天
                        this.today =  this.year + '年' + this.month + '月' +'1日';
                    }
                    for(var j in data.data){
                        var dayFor = data.data[j].time.split('-')[2];
                        for(var k in arr1){
                            if(arr1[k].num == dayFor){
                                if(data.data[j].state == 1){//state”: 3 (1 迟到 2 请假 3 迟到加请假) status （3,2,4）
                                    arr1[k].status = 3
                                }else if(data.data[j].state == 2){
                                    arr1[k].status = 2
                                }else if(data.data[j].state == 3){
                                    arr1[k].status = 4
                                }
                            }
                        }
                    }

                }else{
                    this.$toast(data.msg);
                }
            },err =>{
                this.$toast('出错了');
            })
            this.emptyDay = arr;
            this.listDay = arr1;
        },
        clickThisDay(index){
            var list = this.listDay;
            for(var i in list){
                if(list[i].status == 1){
                    list[i].status = 5
                }
            }
            if(list[index].status == 2 || list[index].status == 3 || list[index].status == 4){

            }else{
                list[index].status = 1;
            }
            this.listDay = list;
            this.today =  this.year + '年' + this.month + '月' + list[index].num +'日';
            var mon = parseInt(this.month);
            mon = mon < 10 ? '0' + mon : mon;
            var day = parseInt(list[index].num);
            day = day < 10 ? '0' + day : day;
            var time = this.year + '-' + mon + '-' + day;
            this.clickDay = time;
            this.getTimetag(time);  
        },
        getTimetag(time){
            var that = this;
            var param = {
                classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                time : time
            }
            this.$http({url:this.$api.classattendance.getTimetag},param).then(data => {
                console.log(data);  
                if(data.code == 0){
                    that.lateNumber = data.data.lateNumber;
                    that.signBack = data.data.signBack;
                    that.signCount = data.data.signCount;
                }
            })
        }
    },
    created() {
        this.getTody();
        this.getMonthDate(this.year,this.month,this.day,0);
        var mon = parseInt(this.month);
        mon = mon < 10 ? '0' + mon : mon;
        var day = parseInt(this.day);
        day = day < 10 ? '0' + day : day;
        this.clickDay = this.year+'-'+mon+'-'+day;
        this.getTimetag(this.clickDay);
    },
}
</script>

<style scoped>
.calendarBox{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-x: hidden;overflow-y: auto;}
.calendar-top{width: 100%;height: 0.97rem;padding: 0 0.3rem;box-sizing: border-box;}
.prev,.next{width: 1.2rem;height: 100%;text-align: center;line-height: 0.97rem;}
.today{text-align: center;width: 4.5rem;line-height: 0.97rem;}
.week{width: 100%;padding: 0 0.11rem;box-sizing: border-box;overflow: hidden;}
.weekDay{width: 1.04rem;height: 0.58rem;text-align: center;line-height: 0.4rem;font-size: 0.2rem;color: #333;}
.monthbox{width: 100%;padding: 0.02rem 0.02rem 0.24rem 0.02rem;box-sizing: border-box;overflow: hidden;border-bottom: 1px solid #f2f2f2;}
.days{width: 0.5rem;height: 0.5rem;margin: 0.11rem 0.28rem;text-align: center;line-height: 0.5rem;border-radius: 50%;box-sizing: border-box;}
/* .cToday{background: url('../../../assets/index/cToday.png') no-repeat;background-size:100% 100%;color: #fff !important; } */
.cToday{border: 1px solid red;}
.cLeave{background: url('../../../assets/index/cLave.png') no-repeat;background-size:100% 100%;color: #fff !important; }
.cLate{background: url('../../../assets/index/cLate.png') no-repeat;background-size:100% 100%;color: #fff !important; }
.cBoth{background: url('../../../assets/index/cBoth.png') no-repeat;background-size:100% 100%;color: #fff !important; }
.chooseType{width: 100%;border-bottom: 1px solid #f2f2f2;}
.type-list{margin-left: 0.3rem;border-bottom:1px solid #f2f2f2;line-height: 0.97rem;overflow: hidden; height: 0.97rem;}
.type-img{width: 0.3rem;height: 0.3ren;display: block;margin: 0.33rem 0.3rem 0 0.2rem;}
.borderNone{border: none !important;}
.tuliBox{padding: 0 0.3rem;}
.tu-list{overflow: hidden;margin-top: 0.34rem;}
.tu-list img{width: 0.2rem;height: 0.2rem;display: block;float: left;margin-top: 0.04rem}
</style>






